function createElement(tag, attrs, ...children) {
  return {
    tag,
    attrs,
    children,
  };
}

class Component {
  constructor(props = {}) {
    this.state = {};
    this.props = props;
  }

  setState(stateChange) {
    // Â∞Ü‰øÆÊîπÂêàÂπ∂Âà∞state
    Object.assign(this.state, stateChange);
    renderComponent(this);
  }
}

function createComponent(component, props) {

  let inst;
  // Â¶ÇÊûúÊòØÁ±ªÂÆö‰πâÁªÑ‰ª∂ÔºåÂàôÁõ¥Êé•ËøîÂõûÂÆû‰æã
  if (component.prototype && component.prototype.render) {
    inst = new component(props);
    // Â¶ÇÊûúÊòØÂáΩÊï∞ÂÆö‰πâÁªÑ‰ª∂ÔºåÂàôÂ∞ÜÂÖ∂Êâ©Â±ï‰∏∫Á±ªÂÆö‰πâÁªÑ‰ª∂
  } else {
    inst = new Component(props);
    inst.constructor = component;
    inst.render = function() {
      return this.constructor(props);
    };
  }

  return inst;
}

function setComponentProps(component, props) {

  if (!component.base) {
    if (component.componentWillMount) component.componentWillMount();
  } else if (component.componentWillReceiveProps) {
    component.componentWillReceiveProps(props);
  }

  component.props = props;

  renderComponent(component);

}

function renderComponent(component) {

  let base;

  const renderer = component.render();

  if (component.base && component.componentWillUpdate) {
    component.componentWillUpdate();
  }
  console.log('component.base');
  console.log(component.base);
  console.log('renderer');
  console.log(JSON.stringify(renderer));
  //base = _render(renderer);
  base = diff(component.base, renderer);
  if (component.base) {
    if (component.componentDidUpdate) component.componentDidUpdate();
  } else if (component.componentDidMount) {
    component.componentDidMount();
  }

  //if (component.base && component.base.parentNode) {
  //  component.base.parentNode.replaceChild(base, component.base);
  //}

  component.base = base;
  base._component = component;

}

function diff(dom, vnode, container) {

  const ret = diffNode(dom, vnode);

  if (container && ret.parentNode !== container) {
    container.appendChild(ret);
  }

  return ret;

}

function diffNode(dom, vnode) {
  console.log('diffnode');
  console.log(dom);
  console.log(vnode);
  let out = dom;

  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';

  if (typeof vnode === 'number') vnode = String(vnode);

  // diff text node
  if (typeof vnode === 'string') {
  console.log('1111')
    // Â¶ÇÊûúÂΩìÂâçÁöÑDOMÂ∞±ÊòØÊñáÊú¨ËäÇÁÇπÔºåÂàôÁõ¥Êé•Êõ¥Êñ∞ÂÜÖÂÆπ
    if (dom && dom.nodeType === 3) {    // nodeType: https://developer.mozilla.org/zh-CN/docs/Web/API/Node/nodeType
      if (dom.textContent !== vnode) {
        dom.textContent = vnode;
      }
      // Â¶ÇÊûúDOM‰∏çÊòØÊñáÊú¨ËäÇÁÇπÔºåÂàôÊñ∞Âª∫‰∏Ä‰∏™ÊñáÊú¨ËäÇÁÇπDOMÔºåÂπ∂ÁßªÈô§ÊéâÂéüÊù•ÁöÑ
    } else {
      out = document.createTextNode(vnode);
      if (dom && dom.parentNode) {
        dom.parentNode.replaceChild(out, dom);
      }
    }

    return out;
  }

  if (typeof vnode.tag === 'function') {
    console.log('2222')
    return diffComponent(dom, vnode);
  }

  //
  if (!dom || !isSameNodeType(dom, vnode)) {
    console.log('3333')
    out = document.createElement(vnode.tag);

    if (dom) {
      [...dom.childNodes].map(out.appendChild);    // Â∞ÜÂéüÊù•ÁöÑÂ≠êËäÇÁÇπÁßªÂà∞Êñ∞ËäÇÁÇπ‰∏ã

      if (dom.parentNode) {
        dom.parentNode.replaceChild(out, dom);    // ÁßªÈô§ÊéâÂéüÊù•ÁöÑDOMÂØπË±°
      }
    }
  }

  if (vnode.children && vnode.children.length > 0 || (out.childNodes && out.childNodes.length > 0)) {
    console.log('4444')
    diffChildren(out, vnode.children);
  }

  diffAttributes(out, vnode);

  return out;

}

function isSameNodeType(dom, vnode) {
  if (typeof vnode === 'string' || typeof vnode === 'number') {
    return dom.nodeType === 3;
  }

  if (typeof vnode.tag === 'string') {
    return dom.nodeName.toLowerCase() === vnode.tag.toLowerCase();
  }

  return dom && dom._component && dom._component.constructor === vnode.tag;
}

function diffAttributes(dom, vnode) {

  const old = {};    // ÂΩìÂâçDOMÁöÑÂ±ûÊÄß
  const attrs = vnode.attrs;     // ËôöÊãüDOMÁöÑÂ±ûÊÄß

  for (let i = 0; i < dom.attributes.length; i++) {
    const attr = dom.attributes[i];
    old[attr.name] = attr.value;
  }

  // Â¶ÇÊûúÂéüÊù•ÁöÑÂ±ûÊÄß‰∏çÂú®Êñ∞ÁöÑÂ±ûÊÄßÂΩì‰∏≠ÔºåÂàôÂ∞ÜÂÖ∂ÁßªÈô§ÊéâÔºàÂ±ûÊÄßÂÄºËÆæ‰∏∫undefinedÔºâ
  for (let name in old) {

    if (!(name in attrs)) {
      setAttribute(dom, name, undefined);
    }

  }

  // Êõ¥Êñ∞Êñ∞ÁöÑÂ±ûÊÄßÂÄº
  for (let name in attrs) {

    if (old[name] !== attrs[name]) {
      setAttribute(dom, name, attrs[name]);
    }
  }
}

function diffChildren(dom, vchildren) {

  const domChildren = dom.childNodes;
  const children = [];

  const keyed = {};

  if (domChildren.length > 0) {
    for (let i = 0; i < domChildren.length; i++) {
      const child = domChildren[i];
      const key = child.key;
      if (key) {
        keyedLen++;
        keyed[key] = child;
      } else {
        children.push(child);
      }
    }
  }

  if (vchildren && vchildren.length > 0) {

    let min = 0;
    let childrenLen = children.length;

    for (let i = 0; i < vchildren.length; i++) {

      const vchild = vchildren[i];
      const key = vchild.key;
      let child;

      if (key) {

        if (keyed[key]) {
          child = keyed[key];
          keyed[key] = undefined;
        }

      } else if (min < childrenLen) {

        for (let j = min; j < childrenLen; j++) {

          let c = children[j];

          if (c && isSameNodeType(c, vchild)) {

            child = c;
            children[j] = undefined;

            if (j === childrenLen - 1) childrenLen--;
            if (j === min) min++;
            break;

          }

        }

      }

      child = diffNode(child, vchild);

      const f = domChildren[i];
      if (child && child !== dom && child !== f) {
        if (!f) {
          dom.appendChild(child);
        } else if (child === f.nextSibling) {
          removeNode(f);
        } else {
          dom.insertBefore(child, f);
        }
      }

    }
  }

}

function diffComponent(dom, vnode) {

  let c = dom && dom._component;
  let oldDom = dom;

  // Â¶ÇÊûúÁªÑ‰ª∂Á±ªÂûãÊ≤°ÊúâÂèòÂåñÔºåÂàôÈáçÊñ∞set props
  if (c && c.constructor === vnode.tag) {
    setComponentProps(c, vnode.attrs);
    dom = c.base;
    // Â¶ÇÊûúÁªÑ‰ª∂Á±ªÂûãÂèòÂåñÔºåÂàôÁßªÈô§ÊéâÂéüÊù•ÁªÑ‰ª∂ÔºåÂπ∂Ê∏≤ÊüìÊñ∞ÁöÑÁªÑ‰ª∂
  } else {

    if (c) {
      unmountComponent(c);
      oldDom = null;
    }

    c = createComponent(vnode.tag, vnode.attrs);

    setComponentProps(c, vnode.attrs);
    dom = c.base;

    if (oldDom && dom !== oldDom) {
      oldDom._component = null;
      removeNode(oldDom);
    }

  }
  return dom;
}

const React = {
  createElement,
  createComponent,
  Component,
  render,
};

function render(vnode, container) {
  return container.appendChild(_render(vnode));
}

function _render(vnode) {

  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';

  if (typeof vnode === 'number') vnode = String(vnode);

  if (typeof vnode === 'string') {
    let textNode = document.createTextNode(vnode);
    return textNode;
  }
  if (typeof vnode.tag === 'function') {

    const component = createComponent(vnode.tag, vnode.attrs);

    setComponentProps(component, vnode.attrs);

    return component.base;
  }

  const dom = document.createElement(vnode.tag);

  if (vnode.attrs) {
    Object.keys(vnode.attrs).forEach(key => {
      const value = vnode.attrs[key];
      setAttribute(dom, key, value);
    });
  }

  vnode.children.forEach(child => render(child, dom));    // ÈÄíÂΩíÊ∏≤ÊüìÂ≠êËäÇÁÇπ
  return dom;
}

function setAttribute(dom, name, value) {
  // Â¶ÇÊûúÂ±ûÊÄßÂêçÊòØclassNameÔºåÂàôÊîπÂõûclass
  if (name === 'className') name = 'class';

  // Â¶ÇÊûúÂ±ûÊÄßÂêçÊòØonXXXÔºåÂàôÊòØ‰∏Ä‰∏™‰∫ã‰ª∂ÁõëÂê¨ÊñπÊ≥ï
  if (/on\w+/.test(name)) {
    name = name.toLowerCase();
    dom[name] = value || '';
    // Â¶ÇÊûúÂ±ûÊÄßÂêçÊòØstyleÔºåÂàôÊõ¥Êñ∞styleÂØπË±°
  } else if (name === 'style') {
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (value && typeof value === 'object') {
      for (let name in value) {
        // ÂèØ‰ª•ÈÄöËøástyle={ width: 20 }ËøôÁßçÂΩ¢ÂºèÊù•ËÆæÁΩÆÊ†∑ÂºèÔºåÂèØ‰ª•ÁúÅÁï•ÊéâÂçï‰Ωçpx
        dom.style[name] = typeof value[name] === 'number' ? value[name] + 'px' : value[name];
      }
    }
    // ÊôÆÈÄöÂ±ûÊÄßÂàôÁõ¥Êé•Êõ¥Êñ∞Â±ûÊÄß
  } else {
    if (name in dom) {
      dom[name] = value || '';
    }
    if (value) {
      dom.setAttribute(name, value);
    } else {
      dom.removeAttribute(name);
    }
  }
}

export default React;